<template>
	<div class="container">
        <v-back title="实时查寝" />
		<div class="form">
            <md-field>
				<md-field-item
				  title="选择学院"
				  :content="selectorValue2"
				  @click="showSelector2"
				  arrow
				  solid
				/>
                <md-field-item
                  title="选择宿舍楼"
                  :content="selectorValue1"
                  @click="showSelector1"
                  arrow
                  solid
                />
                <md-field-item
				v-show="selectorValue1 != '' && selectorValue1 != '请选择'"
                  title="选择楼层"
                  :content="selectorValue3"
                  @click="showSelector3"
                  arrow
                  solid
                />
                <md-field-item
				v-show="selectorValue1 != '' && selectorValue1 != '请选择'"
                  title="选择房间号"
                  :content="selectorValue4"
                  @click="showSelector4"
                  arrow
                  solid
                />
				<md-field-item
				v-show="selectorValue2 != '' && selectorValue2 != '请选择'"
				  title="选择专业"
				  :content="selectorValue5"
				  @click="showSelector5"
				  arrow
				  solid
				/>
				<md-field-item
				v-show="selectorValue2 != '' && selectorValue2 != '请选择'"
				  title="选择班级"
				  :content="selectorValue6"
				  @click="showSelector6"
				  arrow
				  solid
				/>
            </md-field>
			<md-selector
                v-model="isSelectorShow1"
                :data="data1"
                min-height="320px"
                max-height="320px"
                title="选择宿舍楼"
                okText="确认"
                large-radius
                @confirm="onSelectorConfirm1"
                >
            </md-selector>
			<md-selector
                v-model="isSelectorShow2"
                :data="data2"
                min-height="320px"
                max-height="320px"
                title="选择学院"
                okText="确认"
                large-radius
                @confirm="onSelectorConfirm2"
                >
            </md-selector>
            <md-selector
                v-model="isSelectorShow3"
                :data="data3"
                min-height="320px"
                max-height="320px"
                title="选择楼层"
                okText="确认"
                large-radius
                @confirm="onSelectorConfirm3"
                >
            </md-selector>
            <md-selector
                v-model="isSelectorShow4"
                :data="data4"
                min-height="320px"
                max-height="320px"
                title="选择房间号"
                okText="确认"
                large-radius
                @confirm="onSelectorConfirm4"
                >
            </md-selector>
			<md-selector
			    v-model="isSelectorShow5"
			    :data="data5"
			    min-height="320px"
			    max-height="320px"
			    title="选择专业"
			    okText="确认"
			    large-radius
			    @confirm="onSelectorConfirm5"
			    >
			</md-selector>
			<md-selector
			    v-model="isSelectorShow6"
			    :data="data6"
			    min-height="320px"
			    max-height="320px"
			    title="选择班级"
			    okText="确认"
			    large-radius
			    @confirm="onSelectorConfirm6"
			    >
			</md-selector>
			<md-field>
                <md-button type="primary" size="small" round @click="submit">查询</md-button>
                
                <md-landscape v-model="showFullScreen" :full-screen="true">
                    <div style="text-align: left;padding: 4vw;font-size: 4vw;line-height: 6vw;">
                        <div style="margin-bottom: 4vw;">详细信息</div>
                        <div class="flex align_center" style="background-color: #1acfc6;box-sizing: border-box;padding: 2vw;border-radius: 1vw;">
                            <img v-if="showData.photo" :src="showData.photo" alt="" style="box-sizing: border-box;width: 30vw;margin: 0 auto;display: block;box-shadow: 2px 2px 5px rgba(0,0,0,.2);border: 1px solid #f5f5f5;"  @click="imgsShow(showData.photo)">
                            <img v-else src="@/assets/student.png" alt="" style="box-sizing: border-box;width: 30vw;margin: 0 auto;display: block;box-shadow: 2px 2px 5px rgba(0,0,0,.2);border: 1px solid #f5f5f5;">
							<div class="flex btns">
							    <div>
							        <div @click="statusChange(1)">在寝</div>
							    </div>
							    <div>
							        <div @click="statusChange(0)">未归</div>
							    </div>
							    <div>
							        <div @click="statusChange">实习</div>
							    </div>
							    <div>
							        <div @click="statusChange">请假</div>
							    </div>
								<!-- <div>
								    <div @click="statusChange">毕业</div>
								</div> -->
							</div>
						</div>
						<div class="person_list_text">
							<div class="flex">
								状态：
								<div class="full_status">
									{{ personStatusText(showData) }} | 
									<span :class="showData.last_person_entry_exit == null ? 'noactive' : showData.last_person_entry_exit.in_dorm_status == '0' ? '' : 'active'">{{statusText(showData)}}</span>
								</div>
							</div>
							<div class="flex align_center" v-show="personStatusText(showData) == '请假'">请假/实习：{{aroundTime}} <md-icon name="id-card" size="lg" @click="imgsShow(showData.person_io_leave_practices[0].photo)"></md-icon></div>
							<div class="flex">抓拍图片：<img v-if="showData.last_person_entry_exit && showData.last_person_entry_exit.snap_photo" style="width: 15vw;height: 15vw;" @click="imgsShow(showData.last_person_entry_exit.snap_photo)" :src="showData.last_person_entry_exit.snap_photo" alt=""></div>
							<div>学生姓名：{{showData.username}}</div>
							<div>学生学号：{{showData.usernbr}}</div>
							<div>学院名称：{{showData.org_names}}</div>
							<div>班级名称：{{showData.class_names || '--'}}</div>
							<div>住宿信息：{{showData.building_name}}{{showData.dorm_nbr || '--'}}间{{showData.bed_nbr || '--'}}床</div>
							<div>最后通行：<span style="font-size: 3.6vw;font-weight: bolder;" :style="showData.last_person_entry_exit ? (showData.last_person_entry_exit.entry_exit_type == '1' ? 'color: #1acfc6;' : showData.last_person_entry_exit.entry_exit_type == '2' ? 'color: red;' : 'color: #ccc;') : 'color: #ccc;'">({{showData.last_person_entry_exit ? (showData.last_person_entry_exit.entry_exit_type == '1' ? '进' : showData.last_person_entry_exit.entry_exit_type == '2' ? '出' : '未知') : '未知'}})</span> {{showData.last_person_entry_exit ? showData.last_person_entry_exit.snap_camera_name : ''}} {{showData.last_person_entry_exit ? showData.last_person_entry_exit.entry_exit_time : '--'}}</div>
							<div>备注：{{showData.last_person_entry_exit ? showData.last_person_entry_exit.remark : '--'}}</div>
							<md-button type="primary" size="small" round @click="toRecord" style="margin-top: 4vw;">通行记录</md-button>
						</div>
                    </div>
                    

                </md-landscape>
            </md-field>
            <div style="font-size: 4vw;text-align: left;line-height: 6vw;padding: 0 4vw;color: #666;">
                <div class="flex center" style="justify-content: space-around;padding: 4vw 0; font-size: 3.6vw; font-weight: bolder;">
                    <div style="width: 30%;" :class="tabIndex == 0 ? 'tab_active' : ''" @click="tabClick(0)">
                        <div>查询学生总数</div>
                        <div>{{total}}人</div>
                    </div>
                    <div style="width: 23%;" :class="tabIndex == 1 ? 'tab_active' : ''" @click="tabClick(1)">
                        <div>在寝</div>
                        <div>{{entry}}人</div>
                    </div>
                    <div style="width: 23%;" :class="tabIndex == 2 ? 'tab_active' : ''" @click="tabClick(2)">
                        <div>未归</div>
                        <div>{{exit}}人</div>
                    </div>
                    <div style="width: 24%;" :class="tabIndex == 3 ? 'tab_active' : ''" @click="tabClick(3)">
                        <div>无记录</div>
                        <div>{{norecord}}人</div>
                    </div>
                </div>
				<div style="height: 88vh;overflow-y: auto;">
					<div class="list" v-for="(item ,index) in copyList" :key="index" @click="fullShow(item)">
						<div class="flex">
							<img v-if="item.photo" :src="item.photo" alt="" @click.stop="imgsShow(item.photo)">
							<img v-else src="@/assets/student.png" alt="">
							<div>
								<div class="flex"><h4>{{item.username}}</h4><span :class="item.last_person_entry_exit == null ? 'noactive' : item.last_person_entry_exit.in_dorm_status == '0' ? '' : 'active'">[{{statusText(item)}}]</span></div>
								<div>{{item.college_name}}{{item.org_names}}{{item.class_names}}</div>
								<div>{{item.building_name}}{{item.dm_dorm.floorNbr}}层{{item.dorm_nbr}}间{{item.bed_nbr}}床</div>
							</div>
						</div>
					   <!-- <h3 class="bolder">{{item.list[0].building_name}} {{item.dorm_nbr}}宿舍</h3>
						<div class="flex">
							<div @click="fullShow(itm)" :class="itm.last_person_entry_exit == null ? 'noactive' : itm.last_person_entry_exit.in_dorm_status == '0' ? '' : 'active'" v-for="(itm, idx) in item.list" :key="idx">
								[{{statusText(itm)}}]
								<span> {{itm.username}}</span>
							</div>
						</div> -->
					</div>
					<div v-if="copyList.length == 0" style="background: #fff;margin: 4vw 0 0;padding: 2vw 0;">
						<md-result-page></md-result-page>
					</div>
				</div>
            </div>
		</div>
		<!-- 图片预览 -->
		<md-image-viewer
			v-model="isViewerShow"
			:list="imgs"
			:has-dots="true">
		</md-image-viewer>
	</div>
</template>

<script>
	var dayjs = require('dayjs')
	export default {
		name: 'realtimecheckingsleep_index',
		data() {
			return {
				isViewerShow: false,
				imgs: [],
				
				
                isSelectorShow1: false,
                data1: [],
                selectorValue1: '',
                value1: '',
                isSelectorShow2: false,
                data2: [],
                selectorValue2: '',
                value2: '',
                isSelectorShow3: false,
                data3: [],
                selectorValue3: '',
                value3: '',
                isSelectorShow4: false,
                data4: [],
                selectorValue4: '',
                value4: '',
				
				isSelectorShow5: false,
				data5: [],
				selectorValue5: '',
				value5: '',
				
				isSelectorShow6: false,
				data6: [],
				selectorValue6: '',
				value6: '',

                showFullScreen: false,
                list: [],
				copyList: [],
                total: '--',
                entry: '--',
                exit: '--',
                norecord: '--',

                showData: {},
				tabIndex: 0,
				aroundTime: ''
			}
		},
		methods: {
			imgsShow(img) {
				this.isViewerShow = true
				this.imgs = [img]
			},
			statusText(item) {
				if (item.last_person_entry_exit) {
					if (item.last_person_entry_exit.in_dorm_status == null) {
						return '无记录'
					} else {
						if (item.last_person_entry_exit.in_dorm_status == '0') {
							return '未归'
						} else {
							if (item.last_person_entry_exit.in_dorm_status == '1') {
								return '在寝'
							} else {
								return '未知'
							}
						}
					}
				} else {
					return '无记录'
				}
			},
			personStatusText(item) {
				let text = '无记录'
				if (item.last_person_entry_exit) {
					if (item.last_person_entry_exit.entry_exit_status != null) {
						if (item.last_person_entry_exit.entry_exit_status == '0') {
							text = '正常'
						}
					}
				}
				if (item.person_io_leave_practices && item.person_io_leave_practices.length > 0) {
					this.aroundTime = dayjs(item.person_io_leave_practices[0].no_attend_starttime).format('YYYY-MM-DD') + ' ~ ' + dayjs(item.person_io_leave_practices[0].no_attend_endtime).format('YYYY-MM-DD')
					if (item.person_io_leave_practices[0].type == 1) {
						text = '请假'
					} else {
						text = '实习'
					}
				}
				return text
			},
            showSelector1() {
                this.isSelectorShow1 = true
            },
			onSelectorConfirm1({text, value}) {
                this.value1 = value
                this.selectorValue1 = text
                this.value3 = ''
                this.selectorValue3 = ''
                if (value != '') {
                    this.getStorey()
                } else {
                    this.data3 = []
                }
            },
            showSelector2() {
                this.isSelectorShow2 = true
            },
			onSelectorConfirm2({text, value}) {
                this.value2 = value
                this.selectorValue2 = text
				this.value5 = ''
				this.selectorValue5 = ''
				this.value6 = ''
				this.selectorValue6 = ''
				this.data6 = []
				if (value != '') {
				    this.getDepartment(this.value2, 1)
				} else {
				    this.data5 = []
				}
            },
            showSelector3() {
                this.isSelectorShow3 = true
            },
			onSelectorConfirm3({text, value}) {
                this.value3 = value
                this.selectorValue3 = text
                this.value4 = ''
                this.selectorValue4 = ''
                if (value != '') {
                    this.getHostel()
                } else {
                    this.data4 = []
                }
            },
            showSelector4() {
                this.isSelectorShow4 = true
            },
			onSelectorConfirm4({text, value}) {
                this.value4 = value
                this.selectorValue4 = text
            },
			showSelector5() {
			    this.isSelectorShow5 = true
			},
			onSelectorConfirm5({text, value}) {
			    this.value5 = value
			    this.selectorValue5 = text
				this.value6 = ''
				this.selectorValue6 = ''
				if (value != '') {
				    this.getDepartment(this.value5, 2)
				} else {
				    this.data6 = []
				}
			},
			showSelector6() {
			    this.isSelectorShow6 = true
			},
			onSelectorConfirm6({text, value}) {
			    this.value6 = value
			    this.selectorValue6 = text
			},
            //打开详情
            fullShow(itm) {
				console.log(itm)
                this.showData = itm
                this.showFullScreen = true
            },
            //改变状态
            statusChange(status) {
				this.$getL('/dorm/last_change', {
				    userId: JSON.parse(localStorage.getItem('user')).user_id,
					username: JSON.parse(localStorage.getItem('user')).username,
				    studentNbr: this.showData.usernbr,
					entry_exit_time: this.showData.last_person_entry_exit ? this.showData.last_person_entry_exit.entry_exit_time : '',
				    status: status
				}, (res)=>{
				    this.$toast.succeed('修改成功')
					let in_dorm_status = '2'
					if (this.showData.last_person_entry_exit) {
						in_dorm_status = this.showData.last_person_entry_exit.in_dorm_status
					}
					let remark = dayjs().format('YYYY-MM-DD HH:mm:ss') + ' ' + JSON.parse(localStorage.getItem('user')).username + ' 手动将人员在寝状态由' + (in_dorm_status == 0 ? '【未归】' : in_dorm_status == 1 ? '【在寝】' : '【未知】') + '修改为' + (status == 0 ? '【未归】' : '【在寝】')
					if (this.showData.last_person_entry_exit) {
						this.showData.last_person_entry_exit.in_dorm_status = status
						this.showData.last_person_entry_exit.remark = remark
						this.showData.last_person_entry_exit.entry_exit_status = 0
					} else {
						this.showData.last_person_entry_exit = {
							in_dorm_status: status,
							entry_exit_status: 0,
							remark: remark
						}
					}
					this.showData.entry_exit_status = 0
					this.showData.in_dorm_status = status
					setTimeout(() => {
						this.$toast.hide()
						this.submit()
					}, 500)
				})
            },
            //查询
            submit() {
                if (this.value1 == '' && this.value2 == '') {
                    this.$toast.failed('请选择学院或宿舍楼栋')
                } else {
                    this.$getL('/dorm/hostel_person', {
                        userId: JSON.parse(localStorage.getItem('user')).user_id,
                        buildingId: this.value1,
                        college_id: this.value2,
                        floorNbr: this.value3,
                        dormId: this.value4,
						org_ids: this.value5,
						class_ids: this.value6
                    }, (res)=>{
                        this.list = res.data
						this.tabClick(this.tabIndex)
                        this.total = res.total
                        this.entry = res.entry
                        this.exit = res.exit
                        this.norecord = res.norecord
                        this.$toast.hide()
                    })
                }
            },
            //获取宿舍楼栋
            getFloor(callback) {
                this.$getL('/dorm/floor', {
					userId: JSON.parse(localStorage.getItem('user')).user_id
				}, (res)=>{
                    this.data1 = [{
                        value: '',
                        text: '请选择'
                    }]
                    res.data.forEach((item) => {
                        this.data1.push({
                            value: item.buildingId,
                            text: item.buildingName
                        })
                    })
					if (callback) callback()
				})
            },
            //获取学院
            getDepartment(parent_org_id, type) {
				let formData = {
					userId: JSON.parse(localStorage.getItem('user')).user_id
				}
				if (parent_org_id != '') {
					formData.parent_org_id = parent_org_id
				}
				if (type == 2) {
					this.$getL('/college/class', {
						org_id: parent_org_id
					}, (res => {
						this.data6 = [{
						    value: '',
						    text: '请选择'
						}]
						res.data.forEach((item) => {
						    this.data6.push({
						        value: item.id,
						        text: item.bj_name
						    })
						})
						this.$toast.hide()
					}))
					return;
				}
				
                this.$getL('/college/department', formData, (res)=>{
					if (type == 0) {
						console.log('666')
						this.data2 = [{
						    value: '',
						    text: '请选择'
						}]
						res.data.forEach((item) => {
						    this.data2.push({
						        value: item.orgId,
						        text: item.orgName
						    })
						})
					}
					if (type == 1) {
						console.log('7777')
						this.data5 = [{
						    value: '',
						    text: '请选择'
						}]
						res.data.forEach((item) => {
						    this.data5.push({
						        value: item.orgId,
						        text: item.orgName
						    })
						})
					}
                    this.$toast.hide()
                })
            },
            //获取楼层
            getStorey() {
                this.$getL('/dorm/storey', {
                    buildingId: this.value1
                }, (res)=>{
                    this.data3 = [{
                        value: '',
                        text: '请选择'
                    }]
                    res.data.forEach((item) => {
                        this.data3.push({
                            value: item.floorNbr,
                            text: item.floorNbr.toString()
                        })
                    })
                    this.$toast.hide()
                })
            },
            //获取房间号
            getHostel() {
                this.$getL('/dorm/hostel', {
                    buildingId: this.value1,
                    floorNbr: this.value3
                }, (res)=>{
                    this.data4 = [{
                        value: '',
                        text: '请选择'
                    }]
                    res.data.forEach((item) => {
                        this.data4.push({
                            value: item.dormId,
                            text: item.dormNbr
                        })
                    })
                    this.$toast.hide()
                })
            },
			tabClick(index) {
				this.tabIndex = index
				if (index == 0) {
					this.copyList = JSON.parse(JSON.stringify(this.list))
				} else if (index == 1) {
					this.copyList = []
					this.list.forEach(item => {
						if (this.statusText(item) == '在寝') {
							this.copyList.push(item)
						}
					})
				} else if (index == 2) {
					this.copyList = []
					this.list.forEach(item => {
						if (this.statusText(item) == '未归') {
							this.copyList.push(item)
						}
					})
				} else if (index == 3) {
					this.copyList = []
					this.list.forEach(item => {
						if (this.statusText(item) == '无记录') {
							this.copyList.push(item)
						}
					})
				}
			},
			toRecord() {
				this.$router.push('/record/index?usernbr=' + this.showData.usernbr + '&username=' + this.showData.username)
			}
        },
        mounted() {
            this.getFloor(() => {
				setTimeout(() => {
					this.getDepartment('', 0)
				}, 200)
			})
        }
	}
</script>

<style scoped="scoped">
    .container {
        height: 100vh;
        overflow-y: auto;
        background-color: #fff;
    }
    .list {
        padding: 3vw;
        margin-bottom: 4vw;
        border: 1px solid #e2e4ea;
    }
	.list h4 {
		margin-right: 2vw;
	}
	.list img {
		width: 20vw;
		height: 23vw;
		margin-right: 4vw;
	}
    .list span.active {
        color: #2f86f6;
    }
    .list span.noactive {
        color: #ccc;
    }
    .list span {
        color: #fdb731;
    }
	.tab_active {
		background-color: #1acfc6;
		color: #fff;
		border-radius: 1vw;
	}
	
</style>
